import { NgModule } from '@angular/core';
import { IonicApp, IonicModule } from 'ionic-angular';
import { MyApp } from './app.component';
import { IonicImageLoader } from 'ionic-image-loader';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { BrowserModule } from '@angular/platform-browser';

@NgModule({
  declarations: [
    MyApp,
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/avatars/avatars.module#Module', name: 'avatars-page', segment: 'avatars', priority: 'low', defaultHistory: ['home-page'] },
    { loadChildren: '../pages/cards/cards.module#Module', name: 'cards-page', segment: 'cards', priority: 'low', defaultHistory: ['home-page'] },
    { loadChildren: '../pages/concurrency/concurrency.module#Module', name: 'concurrency-page', segment: 'concurrency', priority: 'low', defaultHistory: ['home-page'] },
    { loadChildren: '../pages/home/home.module#Module', name: 'home-page', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ng-content/ng-content.module#NgContentPageModule', name: 'ng-content-page', segment: 'ng-content', priority: 'low', defaultHistory: ['home-page'] },
    { loadChildren: '../pages/slider/slider.module#Module', name: 'slider-page', segment: 'slider', priority: 'low', defaultHistory: ['home-page'] },
    { loadChildren: '../pages/virtual-scroll/virtual-scroll.module#Module', name: 'virtual-scroll-page', segment: 'virtual-scroll', priority: 'low', defaultHistory: ['home-page'] }
  ]
}),
    IonicImageLoader.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    SplashScreen,
    StatusBar
  ]
})
export class AppModule {}



